import activityBtn from './activity_btn'
import http from '../axios'

export default {
    name:"activity-child-list",
    components:{activityBtn},
    props:['result'],
    data(){
        return{
            childrenList:[],
        }
    },
    created(){
        var self = this;
        http.getChildrenList(this.result.kid,(response)=>{
            self.childrenList = response.data;
        })
    },
    methods:{
        goDetail(id,childId,lastOne){
            this.$emit("listenToChildEvent",{name:"activity-child-detail",param:{id,childId,lastOne}});
        }
    }
}



